body {
  font: 14px 'Century Gothic', Futura, sans-serif;
  margin: 20px;
}

ol,
ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: '';
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #000;
  float: left;
  font-size: 100px;
  font-weight: bold;
  line-height: 34px;
  height: 100px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 100px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 65px;
}

.game-info {
  margin-left: 20px;
  font-size: 20px;
}

li button {
  font-size: 20px;
  margin: 3px;
}

@media (max-width: 500px) {
  .square {
    font-size: 50px;
    line-height: 34px;
    height: 100px;
    width: 100px;
  }
  .game {
    flex-direction: column;
  }
  .game-info {
    margin-top: 15px;
  }
}
